//AQUI VAMOS A TRAER EL MODULO QUE MUESTRA LA INFO INICIAL DEL PRODUCTO
//Y LE DAMOS LA FUNCION EL CLICK ROUTER

import { Component, OnInit } from '@angular/core';
import { NewsServiceComponent } from './shared/news-service.component';
import { ToastrService } from '../common/services/toastr.service';

declare let toastr
@Component({
  selector: 'app-news-list',
  template: `
      <section class="aae-news-section">
      <article class="container">
        <div class="row">
          <div class="col-md-12">
            <h6 class="titles"><span>NEWS</span> OF DESIGNERS</h6>
          </div>
          <div *ngFor="let news of newss" class="col-md-12">
            <app-news-thumbnail (click)="handeThumbnailClick(news.name)" [news]="news"></app-news-thumbnail>
          </div>
        </div>
      </article>
    </section>
  `,
})
export class NewsListComponent implements OnInit {
  newss: any[]
  constructor(private NewsServiceComponent: NewsServiceComponent, private toastr:ToastrService) {
   }

  ngOnInit() {
    this.newss =  this.NewsServiceComponent.getNewss()
  }

  handeThumbnailClick(newsName)  {
    this.toastr.success(newsName)
  }
}
