import { Router, ActivatedRouteSnapshot, CanActivate } from '@angular/router';
import { Injectable} from '@angular/core';
import { NewsServiceComponent } from '../shared/news-service.component';

@Injectable()
export class NewsRouteActivator implements CanActivate { //Redireccionamiento de los items en caso de no existir
    constructor (private NewsServiceComponent:NewsServiceComponent, private router:Router ){
        
    }
    canActivate (route:ActivatedRouteSnapshot){
      const newsExists = !!this.NewsServiceComponent.getNews(+route.params['id'])

      if(!newsExists)
        this.router.navigate(['./common/errorr'])//Redirecciona al error 404 original
        return newsExists
    }
}